import React from 'react'
import { FormattedMessage } from 'react-intl'
import { DropdownItem, DropdownMenu } from 'reactstrap'
import * as Icon from 'react-feather'
import { useHistory } from 'react-router-dom'

const UserDropdown = (props) => {
  const { logoutAction } = props
  const history = useHistory()

  const handleNavigation = (e, path) => {
    e.preventDefault()
    history.push(path)
  }

  return (
    <DropdownMenu right>
      <DropdownItem
        tag='a'
        href='#'
        onClick={(e) => handleNavigation(e, '/account-info')}
      >
        <Icon.User size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='setting.accountInformation' />
        </span>
      </DropdownItem>
      <DropdownItem
        tag='a'
        href='#'
        onClick={(e) => handleNavigation(e, '/change-password')}
      >
        <Icon.Lock size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='setting.changePassword' />
        </span>
      </DropdownItem>
      <DropdownItem divider />
      <DropdownItem
        tag='a'
        href='#'
        onClick={(e) => handleNavigation(e, '/terms-and-condition')}
      >
        <Icon.Lock size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='setting.termAndCondition' />
        </span>
      </DropdownItem>
      <DropdownItem
        tag='a'
        href='#'
        onClick={(e) => handleNavigation(e, '/privacy-policy')}
      >
        <Icon.Lock size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='setting.privacyPolicy' />
        </span>
      </DropdownItem>
      <DropdownItem
        tag='a'
        href='#'
        onClick={(e) => handleNavigation(e, '/language')}
      >
        <Icon.Lock size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='setting.language' />
        </span>
      </DropdownItem>
      <DropdownItem
        tag='a'
        href='#'
        onClick={(e) => handleNavigation(e, '/contact')}
      >
        <Icon.Lock size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='setting.contact' />
        </span>
      </DropdownItem>
      <DropdownItem divider />
      <DropdownItem tag='a' onClick={logoutAction}>
        <Icon.Power size={14} className='mr-50' />
        <span className='align-middle'>
          <FormattedMessage id='navbar.logout' />
        </span>
      </DropdownItem>
    </DropdownMenu>
  )
}

export default UserDropdown
